                    "
                    :loading-icon="require('@/assets/images/icon-default.png')"
                    :error-icon="require('@/assets/images/icon-default.png')"
                  />
                  <img
                    v-if="row?.chain"
                    class="icon-svg icon-network"
                    :src="`${domain}chain/${row?.chain}.png`"
                    alt=""
                    srcset=""
                  />
                </div>
                <div>
                  <div class="flex-start">
                    <span class="name">
                      {{ row?.symbol || '--' }}
                      <span style="font-size: 12px; color: #999"></span>
                    </span>
                    <van-tag class="ml-5" plain size="mini" type="primary">{{ row.type == 'hot' ? '热门': '异动' }}</van-tag>
                  </div>
                  <div class="bottom">
                    <span>
                      {{
                        row?.address?.slice(0, 6) +
                        '***' +
                        row?.address?.slice(-4)
                      }}
                    </span>
                    <i
                      class="iconfont icon-fuzhi ml-5"
                      v-copy="row.address"
                    ></i>
                  </div>
                </div>
              </div>
            </div>
            <div class="flex-between mt_10">
              <span class="remark ellipsis"
                >备注：{{ row.remark || '--' }}</span
              >
              <!-- <div>
              <van-tag
                v-if="row.start_time * 1000 <= Date.now() && row.end_time * 1000 >= Date.now()"
                type="success"
                round
              >
                生效
              </van-tag>
              <van-tag
                v-else-if="row.start_time * 1000 > Date.now()"
                color="#ccc"
                type="danger"
                round
              >
                未开始
              </van-tag>
              <van-tag v-else type="danger" round>失效</van-tag>
            </div> -->
              <div>
                <van-tag plain type="primary" @click.stop="handelEdit(row)"
                  >编辑</van-tag
                >
                &nbsp;
                <van-tag plain type="danger" @click.stop="handelRemove(row)"
                  >删除</van-tag
                >
              </div>
            </div>
          </div>
        </div>
      </div>
      <van-empty v-else-if="tableData.length ==0 && !dataLoading" description="暂无数据" />
      <van-loading class="mt-10" v-else size="20px"/>

      <a href="" class="add" @click.stop.prevent="handelAdd">
        <van-icon name="plus" />
        <span>添加</span>
      </a>
    </div>
  </div>
  <van-dialog
    v-model:show="dialogVisible"
    :title="dialogTitle"
    :showConfirmButton="false"
    closeOnClickOverlay
    style="background-color: #fff; padding: 0 16px"
    closeable
  >
    <van-form ref="form" @submit="onSubmit">
      <div class="in-item">
        <van-field
          label="Token地址"
          required
          :rules="[{ required: true, message: '请填写Token地址' }]"
          v-model="form.address"
          name="token"
          placeholder="输入Token地址"
          style="background: #f8f8f8"
        />
      </div>
      <div class="in-item">
        <span>选择公链</span>
        <div @click="showChain = true">
          <img
            class="icon-svg icon-network"
            :src="`${domain}chain/${form.chain}.png`"
            alt=""
            srcset=""
          />
          {{ form.chain }}
          <van-icon name="arrow" size="14" color="#D2D2D2" />
        </div>
      </div>
      <div class="in-item" @click="showStartTime = true">
        <span>开始时间</span>
        <div>
          <span v-if="form?.start_time">{{
            $f.formatDate(form?.start_time, 'YYYY-MM-DD HH:mm')
          }}</span>
          <van-icon name="arrow" size="14" color="#D2D2D2" />
        </div>
      </div>
      <div class="in-item" @click="showEndTime = true">
        <span>结束时间</span>
        <div>
          <span v-if="form?.end_time">{{
            $f.formatDate(form?.end_time, 'YYYY-MM-DD HH:mm')
          }}</span>
          <van-icon name="arrow" size="14" color="#D2D2D2" />
        </div>
      </div>
      <div class="in-item">
        <van-field
          label="热搜币顺序"
          v-model="form.front_show_index"
