        />
      </div>
      <span
        class="block"
        style="font-size: 12px; text-align: left; color: #999"
      >
        * 数字越小显示越靠前
      </span>
      <div class="in-item" @click="showType = true">
        <span>类型</span>
        <div class="flex-1"></div>
        {{ form.type == 'hot' ? '热门': '异动' }}
        <van-icon name="arrow" size="14" color="#D2D2D2" />
      </div>
      <div class="in-item">
        <van-field
          label="备注"
          v-model="form.remark"
          name="remark"
          placeholder="输入备注"
          style="background: #f8f8f8"
        />
      </div>
      <van-button
        class="btn"
        type="primary"
        native-type="submit"
        :loading="isLoading"
      >
        确认
      </van-button>
    </van-form>
  </van-dialog>
  <van-action-sheet
    v-model:show="showChain"
    cancel-text="取消"
    description="选择链"
