      <van-cell
        v-for="item in typeList"
        :key="item.id"
        class="list-item"
        @click.stop="selectType(item)"
        :title="item.name"
        :value="item.id"
        clickable
        :center="true"
      >
      </van-cell>
    </div>
  </van-action-sheet>

  <van-popup v-model:show="showStartTime" teleport="#app" position="bottom">
    <van-datetime-picker
      v-model="start_time"
      type="datetime"
      @confirm="confirmTime('start')"
      @cancel="showStartTime = false"
    />
  </van-popup>
  <van-popup v-model:show="showEndTime" teleport="#app" position="bottom">
    <van-datetime-picker
      v-model="end_time"
      type="datetime"
      @confirm="confirmTime('end')"
      @cancel="showEndTime = false"
    />
  </van-popup>
</template>

<script>
import {
  getAlertAdList,
  addAlertAd,
  updateAlertAd,
  removeAlertAd,
  getChainList
} from '@/api/index.js'
export default {
  name: 'AlertAd',
  data () {
    const validateIndex = (tableData, curItem) => (rule, value, callback) => {
      if (value === '' || value === undefined) {
        callback(new Error('请输入热搜币序号'))
      } else {
        // const isRepeat = tableData.some(i => i.front_show_index === value && !(i.start_time * 1000 <= Date.now() && i.end_time * 1000 >= Date.now()) && !i.is_delete) && curItem.front_show_index !== value
        // if (isRepeat) {
        //   callback(new Error('不能输入已存在的序号'))
        // } else {
        //   callback()
        // }
        callback()
      }
    }
    return {
      form: {
        address: '',
        chain: '',
        front_show_index: undefined,
        remark: '',
        hot_rank: 0,
        type: '',
        is_ignorepricechange: 0,
        start_time: null,
        end_time: null
      },
      tableData: [],
      dataLoading: true,
      loading: {},
      dialogVisible: false,
      curItem: {},
      dialogTitleType: 1,
      addLoading: false,
      updateLoading: false,
      validateIndex,
      showAdd: false,
      showChain: false,
      domain: 'https://www.logofacade.com/',
      chainList: [],
      showStartTime: false,
      showEndTime: false,
      show: false,
      start_time: null,
      end_time: null,
      initTableData: [],
      query: '',
      Timer: null,
      typeList: [
        {
          name: '热门',
          id: 'hot',
        },
        {
          name: '异动',
          id: 'alert',
        },
      ],
      showType: false,
    }
  },
  computed: {
    dialogTitle() {
      const o = {
        1: '添加',
        2: '编辑',
      }
      return o[this.dialogTitleType]
    },
    totalFee() {
      return this.tableData.reduce((a, c) => c.fee + a, 0)
    },
    rules() {
      return {
        address: [
          { required: true, message: '请输入token地址', trigger: 'blur' },
        ],
        chain: [{ required: true, message: '请输入链', trigger: 'blur' }],
        time: [{ required: true, message: '请输入时间', trigger: 'blur' }],
        fee: [{ required: true, message: '请输入费用', trigger: 'blur' }],
        token_index: [
          { required: true, message: '请输入index', trigger: 'blur' },
        ],
        front_show_index: [
          {
            validator: this.validateIndex(this.tableData, this.curItem),
            trigger: 'blur',
          },
        ],
      }
    },
    role() {
      return this.$store.state.userInfo.role
    },
    isLoading() {
      return this.addLoading || this.updateLoading
    },
  },
  created() {
    this.getAlertAdList()
    this.getChainList()
  },
  methods: {
    search() {
      if (this.Timer) {
        clearTimeout(this.Timer)
      }
      const val = this.query
      this.Timer = setTimeout(() => {
        if (val) {
          const list = this.initTableData?.slice(0) || []
          this.tableData = list?.filter(
            (i) =>
              new RegExp(`^${val}.*$`, 'gi').test(i.symbol) ||
              new RegExp(`^${val}.*$`, 'gi').test(i.address),
          )
        } else {
          this.tableData = this.initTableData
        }
      }, 1000)
    },
    confirmTime(type) {
      if (type === 'start') {
        this.form.start_time = new Date(this.start_time).getTime() / 1000
        this.showStartTime = false
      } else {
        this.form.end_time = new Date(this.end_time).getTime() / 1000
        this.showEndTime = false
      }
    },
    getChainList() {
      getChainList()
        .then((res) => {
          this.chainList = res || []
        })
        .catch(() => {
          this.chainList = []
        })
        .finally(() => {})
    },
    selectChain(item) {
      this.showChain = false
      this.form.chain = item.net_name
    },
    selectType(item) {
      this.showType = false
      this.form.type = item.id
    },

    onConfirmCalendar() {},
    getAlertAdList() {
      this.dataLoading = true
      getAlertAdList()
        .then((res) => {
          this.tableData = res || []
          this.initTableData = res || []
        })
        .catch(() => {
          this.tableData = []
          this.initTableData = []
        })
        .finally(() => {
          this.dataLoading = false
        })
    },
    onSubmit() {
      if (this.dialogTitleType === 1) {
        this.addAlertAd()
      } else {
        this.updateAlertAd()
      }
    },
    addAlertAd() {
      if (this.form?.front_show_index) {
        this.form.front_show_index = Number(this.form?.front_show_index)
      }
      this.addLoading = true
      const data = {
        ...this.form,
        start_time: parseInt(this.form.start_time),
        end_time: parseInt(this.form.end_time),
      }
      addAlertAd(data)
        .then((res) => {
          this.$notify({ type: 'success', message: '添加成功' })
          this.dialogVisible = false
          this.getAlertAdList()
        })
        .catch((err) => {
          this.$notify({ type: 'danger', message: '添加失败' })
        })
        .finally(() => {
          this.addLoading = false
        })
    },
    updateAlertAd() {
      if (this.form?.front_show_index) {
        this.form.front_show_index = Number(this.form?.front_show_index)
      }

      const data = {
        ...this.form,
        start_time: parseInt(this.form.start_time),
        end_time: parseInt(this.form.end_time),
      }
      this.updateLoading = true
      updateAlertAd(data)
        .then((res) => {
          this.$notify({ type: 'success', message: '更新成功' })
          this.dialogVisible = false
          this.getAlertAdList()
        })
        .catch((err) => {
          this.$notify({ type: 'danger', message: '更新失败' })
        })
        .finally(() => {
          this.updateLoading = false
        })
    },
    handelState(row) {
      const data = {
        ...row,
        is_delete: row.is_delete,
      }
      this.loading[row.id] = true
      updateAlertAd(data)
        .then(async (res) => {
          this.$notify({ type: 'success', message: '切换成功' })
        })
        .catch((err) => {
          this.$notify({ type: 'danger', message: err })
        })
        .finally(() => {
          this.loading[row.id] = false
          this.getAlertAdList()
        })
    },
    handelClose() {
      this.$refs.form.resetFields()
    },
    handelAdd() {
      this.form = {
        address: '',
        chain: '',
        front_show_index: undefined,
        remark: 'yep',
        hot_rank: 0,
        is_ignorepricechange: 0,
        start_time: null,
        end_time: null,
      }
      this.form.start_time = Date.now() / 1000
      this.form.end_time = (Date.now() + 60 * 24 * 60 * 1000) / 1000
      this.start_time = new Date(this.form.start_time * 1000)
      this.end_time = new Date(this.form.end_time * 1000)

      this.dialogTitleType = 1
      this.dialogVisible = true
    },
    handelEdit(row) {
      this.dialogTitleType = 2
      this.curItem = row
      this.form = {
        ...row,
      }

      this.start_time = new Date(row.start_time * 1000)
      this.end_time = new Date(row.end_time * 1000)
      this.dialogVisible = true
    },
    handelRemove(row) {
      this.$dialog
        .alert({
          title: '提示',
          message: `确定要删除${row.symbol}广告吗?${row.address}-${row.chain}`,
          closeOnPopstate: true,
          closeOnClickOverlay: true,
          messageAlign: 'left',
        })
        .then(() => {
          removeAlertAd(row.id)
            .then((res) => {
              this.$notify({ type: 'success', message: '删除成功！' })
              this.getAlertAdList()
            })
            .catch((err) => {
              console.log(err)
              this.$notify({ type: 'danger', message: '删除失败！' })
            })
        })
    },
  },
}
</script>

